@import '../../styles/mixins';

$large-size: 1320px;
$medium-size: 1024px;
$small-size: 768px;
$smallest-size: 480px;

$font-size-base: 14;
$line-heaight-base: 17px;
$font-family-base: 'Roboto', sans-serif;
$font-family-solid: 'Montserrat', sans-serif;
$font-family-header: 'Fugaz One', cursive;
$font-family-text: 'Open Sans', sans-serif;

$primary-1: #ae0032;
$primary-2: #455162;
$primary-3: #1f2f46;

$neutral-1: #0d1720;
$neutral-2: #636363;
$neutral-3: #dadada;
$neutral-4: #f2f2f2;
$neutral-5: #fcfbfa;

$gray-1: #F1F1F1;
$gray-2: #404040;
$gray-3: #292929;
$gray-4: #333C30;
$gray-5: #F8F8F8;
$gray-6: #D4D4D4;

$orange: #EB5B1F;
$orange-1:#B44619;
$orange-hover:#DC6331;

$green-1: #1B3A1B;

$btn-width: 478px;
$btn-width-m: 198px;

.slider {
  padding-top: 110px;
  padding-bottom: 230px;
  background-color: $gray-5;
  background-image: url('images/bg.svg');
  background-position-x: right;
  background-position-y: bottom;
  background-repeat: no-repeat;
  position: relative;
  display: flex;
  justify-content: center;
  @include phone {
    padding-top: 40px;
    padding-bottom: 94px;
    padding-left: 15px;
    background-image: url('images/bg-m.svg');
  }
}

.slider-wrapper {
  height: $btn-width;
  position: relative;
  @include phone {
    height: $btn-width-m;
  }

  .swiper-slide {
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }

  .swiper-button-next,
  .swiper-button-prev {
    width: 34px;
    height: 100%;
    position: absolute;
    top: 20px; 
    z-index: 10;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    &::after {
      display: none;
    }
  }

  .swiper-button-next {
    background-color: $orange-1;
    border-color: $orange-1;
    right: 0;
    left: auto;
  }

  .swiper-button-prev {
    background-color: $green-1;
    border-color: $green-1;
    left: 0;
    right: auto;
  }

  .swiper-button-text {
    transform: rotate(270deg);
    padding-top: 5px;
    padding-bottom: 4px;
    text-transform: uppercase;
    font-family: $font-family-header;
    color: white;
    font-size: 16px;
    line-height: 23px;
    letter-spacing: 0.04em;
  }

  .swiper-slide {
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }

  .swiper-button-next,
  .swiper-button-prev {
    width: 34px;
    height: 100%;
    position: absolute;
    top: 0;
    z-index: 10;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    &::after {
      display: none;
    }
    @include phone {
      display: none;
    }
  }

  .swiper-button-next {
    background-color: $orange-1;
    border-color: $orange-1;
    right: 0;
    left: auto;
  }

  .swiper-button-prev {
    background-color: $green-1;
    border-color: $green-1;
    left: 0;
    right: auto;
  }

  .swiper-button-text {
    transform: rotate(270deg);
    padding-top: 5px;
    padding-bottom: 4px;
    text-transform: uppercase;
    font-family: $font-family-header;
    color: white;
    font-size: 16px;
    line-height: 23px;
    letter-spacing: 0.04em;
  }
}

.slider-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.slider-item {
  @include phone {
    height: auto;
    max-width: 320px;
  }
  img {   
    height: $btn-width;
    object-fit: cover;
    @include phone {
      object-fit: contain;
    }
  }
  &:hover {
    .slider-btn {
      opacity: 1;
    }
  }
}

.swiper-slide-active {
  .slider-btn {
    opacity: 1;
  }
}

.slider-btn {
  position: absolute;
  padding: 9px 48px;
  background-color: $orange;
  color: white;
  border-color: $orange;
  opacity: 0;
  transition: all .5s;
  z-index: 20;
}